import {
  supabase,
  Category,
  Product,
  TABLE_NAMES,
  StoreType,
  BayinetProduct,
  DengeProduct,
  OksidProduct,
} from "./supabase";

// Bayinet verilerini normalize et
function normalizeBayinetProduct(item: BayinetProduct): Product {
  return {
    id: item.id,
    name: item.name,
    price: item.price,
    category_id: undefined, // Artƒ±k category_id yok, category string olarak var
    store: "bayinet",
    image_url: item.image_url,
    description: item.description,
    stock_status: item.stock_status,
  };
}

// Denge verilerini normalize et
function normalizeDengeProduct(item: DengeProduct): Product {
  return {
    id: item.id,
    name: item.product_name, // Farklƒ± alan adƒ±
    price: item.cost, // Farklƒ± alan adƒ±
    category_id: undefined, // Artƒ±k category_id yok, category string olarak var
    store: "denge",
    image_url: item.image, // Farklƒ± alan adƒ±
    description: item.details, // Farklƒ± alan adƒ±
    stock_status: item.availability, // Farklƒ± alan adƒ±
  };
}

// Oksid verilerini normalize et - Ger√ßek ≈üema
function normalizeOksidProduct(item: OksidProduct): Product {
  return {
    id: item.id.toString(), // number'dan string'e √ßevir
    name: item.name || "ƒ∞simsiz √úr√ºn", // null olabilir
    price: item.price_1 || item.price_2 || undefined, // ƒ∞ki fiyat alanƒ±ndan birini kullan
    category_id: undefined, // category string olarak geliyor, id yok
    store: "oksid",
    image_url: undefined, // ≈ûemada resim alanƒ± yok
    description: item.url || undefined, // URL'yi a√ßƒ±klama olarak kullan
    stock_status: item.stock || undefined,
    created_at: item.created_at || undefined,
  };
}

// Kategorileri √ºr√ºn tablolarƒ±ndan √ßek - Her maƒüaza i√ßin
export async function getCategoriesByStore(store: string): Promise<Category[]> {
  try {
    const storeKey = store.toLowerCase() as StoreType;

    switch (storeKey) {
      case "bayinet":
        return await getBayinetCategoriesFromProducts();
      case "denge":
        return await getDengeCategoriesFromProducts();
      case "oksid":
        return await getOksidCategoriesFromProducts();
      default:
        console.error(`Unknown store: ${store}`);
        return [];
    }
  } catch (error) {
    console.error("Error in getCategoriesByStore:", error);
    return [];
  }
}

// Bayinet i√ßin √ºr√ºnlerden benzersiz kategori listesi √ßek
async function getBayinetCategoriesFromProducts(): Promise<Category[]> {
  try {
    const { data, error } = await supabase
      .from("bayinet_products")
      .select("category")
      .not("category", "is", null)
      .order("category");

    if (error) {
      console.error("Error fetching Bayinet categories from products:", error);
      return [];
    }

    if (!data) return [];

    // Benzersiz kategorileri √ßƒ±kar ve normalize et
    const uniqueCategories = [...new Set(data.map((item) => item.category))];

    return uniqueCategories
      .filter((category) => category) // null/undefined'larƒ± filtrele
      .map((category, index) => ({
        id: (index + 1).toString(),
        name: category!,
        store: "bayinet",
      }));
  } catch (error) {
    console.error("Error in getBayinetCategoriesFromProducts:", error);
    return [];
  }
}

