// Oksid i√ßin √ºr√ºnlerden benzersiz kategori listesi √ßek
async function getOksidCategoriesFromProducts(): Promise<Category[]> {
  try {
    // Sayfalama ile t√ºm kategori verilerini √ßek
    // ƒ∞lk √∂nce distinct kategorileri √ßekelim - bu performansƒ± artƒ±rƒ±r
    const { data: distinctData, error: distinctError } = await supabase.rpc(
      "get_distinct_oksid_categories"
    );

    // RPC fonksiyonu kullanƒ±labilir deƒüilse (olu≈üturulmadƒ±ysa), t√ºm verileri √ßekerek manuel olarak ayrƒ±≈ütƒ±ralƒ±m
    if (distinctError || !distinctData) {
      console.log(
        "Distinct RPC fonksiyonu kullanƒ±lamƒ±yor, manuel y√∂ntem kullanƒ±lƒ±yor..."
      );

      // Sayfa sayfa veri √ßekelim (paginasyon)
      let allCategories: Array<string> = [];
      let hasMoreData = true;
      let offset = 0;
      const pageSize = 1000;

      while (hasMoreData) {
        const { data: pageData, error: pageError } = await supabase
          .from("oksid_products")
          .select("category")
          .not("category", "is", null)
          .range(offset, offset + pageSize - 1);

        if (pageError) {
          console.error(
            `Error fetching Oksid categories (page ${offset / pageSize + 1}):`,
            pageError
          );
          break;
        }

        if (!pageData || pageData.length === 0) {
          hasMoreData = false;
          break;
        }

        // Bu sayfadaki kategorileri ekle
        const pageCategories = pageData.map(
          (item: { category: string }) => item.category
        );
        allCategories = [...allCategories, ...pageCategories];

        // Sonraki sayfa i√ßin offset'i g√ºncelle
        offset += pageSize;

        // Son sayfaya ula≈ütƒ±ysak d√∂ng√ºden √ßƒ±k
        if (pageData.length < pageSize) {
          hasMoreData = false;
        }

        console.log(
          `Oksid kategorileri sayfa ${offset / pageSize} y√ºklendi. ${
            pageData.length
          } √ºr√ºn, toplam ≈üu ana kadar: ${allCategories.length}`
        );
      }

      // Benzersiz kategorileri √ßƒ±kar
      const uniqueCategories = [...new Set(allCategories)]
        .filter((category: string | null | undefined) => category) // null/undefined'larƒ± filtrele
        .sort((a: string, b: string) => {
          // T√ºrk√ße karakterleri doƒüru sƒ±ralamak i√ßin localeCompare kullan
          return a.localeCompare(b, "tr-TR", {
            sensitivity: "base",
            numeric: true,
          });
        });

      console.log(
        `Toplam ${uniqueCategories.length} benzersiz Oksid kategorisi bulundu.`
      );

      return uniqueCategories.map((category: string, index: number) => ({
        id: (index + 1).toString(),
        name: category,
        store: "oksid",
      }));
    } else {
      // RPC fonksiyonu ba≈üarƒ±yla √ßalƒ±≈ütƒ±, benzersiz kategorileri d√∂nelim
      const uniqueCategories = distinctData
        .filter((category: string | null | undefined) => category) // null/undefined'larƒ± filtrele
        .sort((a: string, b: string) => {
          // T√ºrk√ße karakterleri doƒüru sƒ±ralamak i√ßin localeCompare kullan
          return a.localeCompare(b, "tr-TR", {
            sensitivity: "base",
            numeric: true,
          });
        });

      console.log(
        `RPC ile ${uniqueCategories.length} benzersiz Oksid kategorisi bulundu.`
      );

      return uniqueCategories.map((category: string, index: number) => ({
        id: (index + 1).toString(),
        name: category,
        store: "oksid",
      }));
    }
  } catch (error) {
    console.error("Error in getOksidCategoriesFromProducts:", error);
    return [];
  }
}

// √úr√ºnleri isme g√∂re ara
export async function searchProductsByName(
  searchTerm: string,
  store?: string
): Promise<Product[]> {
  try {
    if (!store) {
      // T√ºm maƒüazalarda ara
      const allResults = await Promise.all([
        searchInStore(searchTerm, "bayinet"),
        searchInStore(searchTerm, "denge"),
        searchInStore(searchTerm, "oksid"),
      ]);
      return allResults.flat();
    }

    return await searchInStore(searchTerm, store.toLowerCase() as StoreType);
  } catch (error) {
    console.error("Error in searchProductsByName:", error);
    return [];
  }
}

// Belirli bir maƒüazada arama yap
async function searchInStore(
  searchTerm: string,
  storeKey: StoreType
): Promise<Product[]> {
  try {
    const tableName = TABLE_NAMES[storeKey]?.products;

    if (!tableName) {
      console.error(`Unknown store: ${storeKey}`);
      return [];
    }

    // Her maƒüaza i√ßin farklƒ± alan adlarƒ±yla arama yap
    let query;
    switch (storeKey) {
      case "bayinet":
        query = supabase
          .from(tableName)
          .select("*")
          .ilike("name", `%${searchTerm}%`)
          .order("name");
        break;
      case "denge":
        query = supabase
          .from(tableName)
          .select("*")
          .ilike("product_name", `%${searchTerm}%`)
          .order("product_name");
        break;
      case "oksid":
        query = supabase
          .from(tableName)
          .select("*")
          .ilike("name", `%${searchTerm}%`) // Ger√ßek ≈üemada 'name' alanƒ± var
          .order("name");
        break;
      default:
        return [];
    }

    const { data, error } = await query;

    if (error) {
      console.error(`Error searching products in ${storeKey}:`, error);
      return [];
    }

    if (!data) return [];

    // Her maƒüaza i√ßin uygun normalize fonksiyonunu kullan
    switch (storeKey) {
      case "bayinet":
        return (data as BayinetProduct[]).map(normalizeBayinetProduct);
      case "denge":
        return (data as DengeProduct[]).map(normalizeDengeProduct);
      case "oksid":
        return (data as OksidProduct[]).map(normalizeOksidProduct);
      default:
        return [];
    }
  } catch (error) {
    console.error(`Error in searchInStore for ${storeKey}:`, error);
    return [];
  }
}

// √úr√ºnleri kategoriye g√∂re getir
export async function getProductsByCategory(
  categoryId: string,
  store?: string
): Promise<Product[]> {
  try {
    if (!store) {
      // T√ºm maƒüazalarda ara
      const allResults = await Promise.all([
        getProductsByCategoryInStore(categoryId, "bayinet"),
        getProductsByCategoryInStore(categoryId, "denge"),
        getProductsByCategoryInStore(categoryId, "oksid"),
      ]);
      return allResults.flat();
    }

    return await getProductsByCategoryInStore(
      categoryId,
      store.toLowerCase() as StoreType
    );
  } catch (error) {
    console.error("Error in getProductsByCategory:", error);
    return [];
  }
}

// Belirli bir maƒüazada kategoriye g√∂re √ºr√ºn getir
async function getProductsByCategoryInStore(
  categoryId: string,
  storeKey: StoreType
): Promise<Product[]> {
  try {
    const tableName = TABLE_NAMES[storeKey]?.products;

    if (!tableName) {
      console.error(`Unknown store: ${storeKey}`);
      return [];
    }

    // Her maƒüaza i√ßin kategori adƒ±yla arama yap
    let query;
    switch (storeKey) {
      case "bayinet":
        query = supabase
          .from(tableName)
          .select("*")
          .eq("category", categoryId) // Artƒ±k hepsi 'category' alanƒ± kullanƒ±yor
          .order("name");
        break;
      case "denge":
        query = supabase
          .from(tableName)
          .select("*")
          .eq("category", categoryId) // Artƒ±k hepsi 'category' alanƒ± kullanƒ±yor
          .order("product_name");
        break;
      case "oksid":
        query = supabase
          .from(tableName)
          .select("*")
          .eq("category", categoryId) // Ger√ßek ≈üemada 'category' alanƒ± var
          .order("name");
        break;
      default:
        return [];
    }

    const { data, error } = await query;

    if (error) {
      console.error(
        `Error fetching products by category in ${storeKey}:`,
        error
      );
      return [];
    }

    if (!data) return [];

    // Her maƒüaza i√ßin uygun normalize fonksiyonunu kullan
    switch (storeKey) {
      case "bayinet":
        return (data as BayinetProduct[]).map(normalizeBayinetProduct);
      case "denge":
        return (data as DengeProduct[]).map(normalizeDengeProduct);
      case "oksid":
        return (data as OksidProduct[]).map(normalizeOksidProduct);
      default:
        return [];
    }
  } catch (error) {
    console.error(
      `Error in getProductsByCategoryInStore for ${storeKey}:`,
      error
    );
    return [];
  }
}

// T√ºm maƒüazalarda √ºr√ºn kar≈üƒ±la≈ütƒ±rmasƒ±
export async function compareProductsAcrossStores(
  searchTerm: string
): Promise<Product[]> {
  try {
    // T√ºm maƒüazalarda paralel arama yap
    const allResults = await Promise.all([
      searchInStore(searchTerm, "bayinet"),
      searchInStore(searchTerm, "denge"),
      searchInStore(searchTerm, "oksid"),
    ]);

    // Sonu√ßlarƒ± birle≈ütir ve fiyata g√∂re sƒ±rala
    return allResults.flat().sort((a, b) => {
      // √ñnce maƒüazaya g√∂re sƒ±rala, sonra fiyata g√∂re
      if (a.store !== b.store) {
        return a.store.localeCompare(b.store);
      }
      return (a.price || 0) - (b.price || 0);
    });
  } catch (error) {
    console.error("Error in compareProductsAcrossStores:", error);
    return [];
  }
}
